@import url('https://fonts.googleapis.com/css2?family=Archivo:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
body {
  font-family: 'Archivo', sans-serif;
}
h1, h2, h3, h4, h5, h6, p {
  margin: 0;
	padding: 0;
	border: 0;
}
.Page {
  min-height: calc(100vh - 194px);
}
.Wrap {
  width: 100%;
  max-width: 1310px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}
a {
  text-decoration: none;
  color: #000;
}
a:hover {
  opacity: 0.7;
  transition: all 0.2s;
}
/* Font Size */
.T30 {
  font-size: 30px;
}
.T24 {
  font-size: 24px;
}
.T22 {
  font-size: 22px;
}
.T16 {
  font-size: 24px;
}
.T14 {
  font-size: 14px;
}
/* Font Color */
.White {
  color: #FFF;
}
.Black {
  color: #000;
}
.Gray {
  color: #7D7D7D;
}
.Red {
  color: #EC0000;
}
/* Fonte Weight */
.ExtraBold {
  font-weight: 800;
}
.Bold {
  font-weight: 700;
}
.Medium {
  font-weight: 500;
}
.Regular {
  font-weight: 400;
}

select {
  background: inherit;
  border: none;
  padding: 0;
}