.Product .Wrap {
    padding: 80px 0;
    width: 1080px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between; 
}
.Product .Wrap .Left{
    width: 50%;
    padding-right: 50px;
}
.Product .Wrap .MainImg{
    width: 100%;
    height: 440px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.Product .Wrap .Right{
    width: 50%;
    padding-left: 50px;
}
.Product .Contents {
    border-left: 2px solid #EC0000;
    padding-left: 30px;
}
.Product .BuyBtn {
    margin-top: 40px;
    color: #FFF;
    text-align: center;
    font-family: Archivo;
    font-size: 20px;
    font-weight: 700;
    border-radius: 50px;
    background: #EC0000;
    padding: 14px 40px;
    box-sizing: border-box;
}
.Product .OrderBtn {
    font-size: 18px;
    width: 100%;
    background: #000;
    color: #FFF;
    font-family: Archivo;
    padding: 14px 0;
    border-radius: 50px;
}
.Product .OrderInput {
    text-align: center;
    font-size: 18px;
    width: 100%;
    border: 1px solid #000;
    color: #000;
    font-family: Archivo;
    padding: 14px 0;
    border-radius: 50px;
}
.Product .OrderComplete {
    text-align: center;
    font-size: 18px;
    width: 100%;
    background: #EC0000;
    color: #FFF;
    font-family: Archivo;
    padding: 14px 0;
    border-radius: 50px;
}
.Product .RadioBtns {
    display: flex;
    align-items: center;
    flex-direction: row;
}
.Product .ImgControl {
    display: flex;
}
.Product .ImgControl > div {
    width: 20%;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.Product .ImgControl > div > img{
    width: 100%;
    cursor: pointer;
}