.Header {
    width: 100%;
    height: 114px;
}
.Header .Wrap{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
}
.Header .Wrap > div{ 
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
}
.Header .Wrap .Logo {
    margin-right: 40px;
}
.Header .Wrap .Menu {
    margin: 0 50px;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}